import React from 'react'
import { LayoutContainer } from '../../shared'

import {
  Container,
  Header,
  Content
} from './styles'

export const Privacy = () => {
  return (
    <LayoutContainer>
      <Container>
        <Header>
          <h2>Privacy Policy - BlockReward GmbH</h2>
        </Header>
        <Content>
          <p>This privacy information informs you about the processing of your personal data when you visit our website (hereinafter "we" or "us").</p> 
          <p><strong>Scope of application of this data protection information</strong></p> 
          <p>This data protection information applies to the use of our websites and the measures related to the purchase for the execution of the contracts in connection with our services, the creation of a customer account, the newsletter, other information e-mails and customer services. </p>
          <p><strong>Person responsible for the processing of your personal data</strong></p> 
          <p>Unless explicitly stated otherwise in this document, the data controller responsible for the processing of your personal data under data protection law:</p> 
          <p>BlockReward GmbH, <br />Elisabethstrasse 15/5A, <br />1010 Vienna, <br />Austria <br />Email: support@BlockReward.com</p> 
          <p><strong>Definitions</strong></p> 
          <p> This data protection information is based on the following key data protection terms, which we have outlined below for ease of understanding:</p> 
          <p> GDPR means the General Data Protection Regulation (Regulation (EU) 2016/679 of the European Parliament and of the Council of 27 April 2016 on the protection of individuals with regard to the processing of personal data, on the free movement of such data and repealing Directive 95/46/EC.</p> 
          <p> Recipient means a natural or legal person, public authority, agency or other body to whom personal data are disclosed, whether or not a third party. However, public authorities that may receive personal data in the context of a specific investigation mandate under Union or Member State law shall not be considered as recipients; the processing of such data by the aforementioned authorities shall be carried out in accordance with the applicable data protection rules, in line with the purposes of the processing.</p> 
          <p>Examples of possible recipients: banks and payment service providers; logistics companies; shipping service providers; IT service providers.</p> 
          <p> Personal data is any information relating to an identified or identifiable natural person ("data subject"). An identifiable natural person is one who can be identified, directly or indirectly, in particular by reference to an identifier such as a name, an identification number, location data, an online identifier or to one or more factors specific to the physical, physiological, genetic, mental, economic, cultural or social identity of that natural person.</p>
          <p>Examples of personal data: Name; contact details; bank and credit card details.</p>
          <p> The controller is the natural or legal person, public authority, agency or other body which alone or jointly with others determines the purposes and means of the processing of personal data. Where the purposes and means of such processing are determined by Union or Member State law, the controller or the specific criteria for its designation may be provided for under Union or Member State law.</p> 
          <p> (Data) processing is any operation or set of operations which is performed upon personal data, whether or not by automatic means, such as collection, recording, organization, filing, storage, adaptation or alteration, retrieval, consultation, use, disclosure by transmission, dissemination or otherwise making available, alignment or combination, restriction, erasure or destruction.</p> 
          <p><strong>Purposes and legal bases of the processing of your personal data</strong></p> 
          <p>When you access our website to learn about our products and services, purchase products from our online store, or otherwise actively submit information to us, we process your personal data for the following purposes and on the basis of the following legal grounds:</p> 
          <p><strong>Website deployment and IT security</strong></p> 
          <p>When you visit our website, we process your personal data that is technically necessary for us to be able to display our website to you and to ensure the stability and security of your visit to our website. For this purpose, we process the following - under certain circumstances - personal data:</p> 
          <p> IP address</p> 
          <p> Browser type and version</p> 
          <p> Operating system and platform</p> 
          <p> the complete Uniform Resource Locator (URL)</p> 
          <p>The aforementioned data is stored in server log files for security purposes. We will store this data for at least 1 month and to a maximum of 6 months.</p> 
          <p>This data processing is necessary for the provision of our website (legal basis: Art 6 para 1 lit b GDPR) and to protect our legitimate interest in ensuring IT security (legal basis: Art 6 para 1 lit f GDPR).</p> 
          <p> When you visit our website, we also process (possibly personal) data such as your language setting in the browser in order to automatically display a localized version, i.e. in particular a version adapted to your language, of our website. </p> 
          <p>This data processing is necessary to protect our legitimate interest in the automated demand-oriented provision of our website (legal basis: Art 6 para 1 lit f GDPR).</p> 
          <p><strong>Google Analytics</strong></p> 
          <p>For analytics, we use Google Analytics, a web analytics service provided by Google LLC ("Google"), on our websites. Google Analytics uses cookies that enable an analysis of the use of our website. The information generated by the cookie about the use of our websites will be transmitted to and stored by Google on servers in the United States. On our behalf, Google will use this information for the purpose of evaluating your use of the website, compiling reports on website activity, analyzing the impact of our digital advertising and customizing it, and providing other services relating to website activity and internet usage. For more information on terms of use and data protection, please see:</p> 
          <p><a target="_blank" href="https://policies.google.com/terms" rel="noreferrer">https://policies.google.com/terms</a></p> 
          <p><a target="_blank" href="https://policies.google.com/privacy" rel="noreferrer">https://policies.google.com/privacy</a></p> 
          <p>This data processing is carried out on the basis of your consent, which we provide via our cookie banner that is displayed to you when you first visit the website (legal basis: Art 6 para 1 lit a GDPR). The transfer of your personal data to the USA, which is a third country within the meaning of the GDPR and a transfer therefore requires a separate legal basis, takes place on the basis of your express consent, which is also requested via the cookie banner (legal basis: Art 49 para 1 lit a GDPR). </p> 
          <p>In addition, you can generally prevent the processing of your usage data by Google on other websites by downloading and installing the browser plug-in available at the following link: <a target="_blank" href="https://tools.google.com/dlpage/gaoptout" rel="noreferrer">https://tools.google.com/dlpage/gaoptout</a></p>
          <p><strong>Registration and creation of a customer account</strong></p> 
          <p>When you visit our websites, you can create a customer account. Registering for a customer account requires you to enter personal data. Mandatory fields are marked accordingly in the input form. The login is performed via the plugin "User Role Editor Pro". Your data will be processed as long as your customer account exists.</p> 
          <p>This data processing is necessary for the execution of a contract (provision of a customer account) with you (legal basis: Art 6 para 1 lit b GDPR).</p> 
          <p><strong>Contact form and data transmission by e-mail</strong></p> 
          <p>Our website contains a contact form that you can use to send us an inquiry. The personal data you provide in the free text field and the fields marked as necessary on the website will be processed. Furthermore, you have the option to contact us by e-mail. In this case, the data that you provide to us in this way will also be processed. Depending on the purpose of your contact, we delete the related data in most cases after the purpose has been achieved or in any case after the end of a legal obligation to retain data.</p> 
          <p>This data processing is necessary for the performance of a contract with you and constitutes a pre-contractual measure (legal basis: Art 6 para 1 lit b GDPR) or serves to respond to a contact request made for other reasons, which we answer based on our legitimate interest (legal basis: Art 6 para 1 lit f GDPR).</p> 
          <p><strong>Revocation of consents</strong></p> 
          <p>If you have given us consent for the processing of your personal data, you can revoke this consent at any time. The revocation of your consent is effective for the future. The lawfulness of the processing of your personal data up to the time of the revocation remains unaffected.</p> 
          <p>Please address your revocation to <a target="_blank" href="mailto:support@BlockReward.com" rel="noreferrer">support@BlockReward.com</a> </p> 
          <p>If you withdraw your consent, we will process your personal data collected in this context to respond to your request. This data processing is necessary to fulfill a legal obligation (legal basis: Art 6 para 1 lit c GDPR).</p> 
          <p><strong>Your further data protection rights</strong></p> 
          <p>In accordance with the GDPR, you may at any time request us to</p> 
          <p> provide you with information about the personal data concerning you that we process (Art 15 GDPR),</p>
          <p> correct personal data concerning you that is inaccurate (Art 16 GDPR),</p> 
          <p> Delete (Art 17 GDPR), restrict (Art 18 GDPR) and/or release/transfer (Art 20 GDPR) your personal data stored by us.</p> 
          <p>Please send your request, including at least your first and last name, by e-mail to support@BlockReward.com or in writing to BlockReward GmbH, Elisabethstrae 15/5a+5b, 1010 Wien.</p> 
          <p>If you assert your rights against us, we process your personal data collected in this context to respond to your request. This data processing is necessary for the fulfillment of a legal obligation (legal basis: Art 6 para 1lit c GDPR).</p> 
          <p>Without prejudice to your rights, you may lodge a complaint with the competent data protection supervisory authority if you consider that the processing of personal data concerning you by us infringes the GDPR (Art 77 GDPR). In Austria, this is the data protection authority (Datenschutzbehrde).</p> 
          <p>Status: June 2022</p> 
          <br />
        </Content>
      </Container>
    </LayoutContainer>
  )
}